  flex-direction: column;
  gap: 32px;
}

.results-content h2 {
  font-size: 28px;
  color: #8b4513;
  margin: 0 0 16px 0;
}

.results-content p {
  font-size: 16px;
  color: #666;
  line-height: 1.6;
  margin: 0 0 24px 0;
}

.result-section {
  background: rgba(255, 255, 255, 0.7);
